// export default Router
const router = createBrowserRouter([
    {
      index: true,
      path: "/",
      element: <HomePage />,
      errorElement: <div> Page not found </div>,
    },
    {
      path: "/pes-2021",
      element: <Pes2021 />,
    },
    {
      path: "/:version/:category/:id",
      element: <Details />
    },
  ])
  
export default function Router() {
  return (
      <RouterProvider router={router}/>
    )
}